.mask
{
    width: 100%;
    height: 100%;
    position: relative;
}
.Intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::before{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.8;
}
.hero
{
    height: 100%;
    width: 100%;
}
.hero .content{
    position:absolute ;
    transform: translate(-50%,-50%);
    top:50%;
    left:50%;
    text-align: center;

}
.hero .content{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}
.hero .content p{
font-size: 1.4rem;
font-weight: 300;
text-transform: uppercase;
}
.content .btn{
    margin: 1rem 0.2rem;
}
@media only screen and (max-width: 768px) {
    .Intro-img{
     width:100%;
     height:100vh;
    }
    .hero .content{
        font-size: 3rem;
        padding: 0.6rem 0 1.5rem;
        top: 30%;
        left: auto;

    
    }
    .hero .content p{
    font-size: 1.4rem;
    }
}