@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', sans-serif;

}
body{
  background-color: black;
}
h1,h4,p,a{
  color: white;
  text-decoration: none;
}
ul{
  list-style-type: none;
}
.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: yellow;
  color: black;
  border: 1px solid white;
  font-weight: 600;
  cursor: pointer;
}
.btn-light{
  background: transparent;
  color: white;
}
.btn:hover{
  background: grey;
  color: white;
  transition: 0.3s;
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
.header-bg{
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}
.nav-menu{
    display: flex;
}
.nav-menu l1{
    padding: 0 1rem;
}
.nav-menu li{
    padding: 0 1rem;
}
.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}
.hamburger{
    display: none;
}
@media screen and (max-width:1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: black;
        position: absolute;
        top: 0;
        left:-100%;
        z-index:-3;
        transition: 0.3s;
    }
    .nav-menu.active{
        left:0;
    }
    .nav-menu li{
        padding: 1rem;
    }
    .nav-menu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial;
    }
}
.mask
{
    width: 100%;
    height: 100%;
    position: relative;
}
.Intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::before{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.8;
}
.hero
{
    height: 100%;
    width: 100%;
}
.hero .content{
    position:absolute ;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    top:50%;
    left:50%;
    text-align: center;

}
.hero .content{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}
.hero .content p{
font-size: 1.4rem;
font-weight: 300;
text-transform: uppercase;
}
.content .btn{
    margin: 1rem 0.2rem;
}
@media only screen and (max-width: 768px) {
    .Intro-img{
     width:100%;
     height:100vh;
    }
    .hero .content{
        font-size: 3rem;
        padding: 0.6rem 0 1.5rem;
        top: 30%;
        left: auto;

    
    }
    .hero .content p{
    font-size: 1.4rem;
    }
}
.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19,19,19,0.8);
}
.footer-container{
   max-width: 1400px;
   margin: auto;
   display: grid;
   grid-template-columns: repeat(2,1fr);
   /* grid-template-columns: auto auto auto; */
   grid-gap:45px;
}
.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location{
    display: flex;
    margin-bottom: 0.8rem;
}
.location p{
    line-height: 30px;

}
.phone p{
    line-height: 20px;
}
.email p{
    line-height: 20px;
}
.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.Social{
    margin-top: 1rem;
    
}
.right p {
    margin-top: 1.5rem;
}
@media screen and (max-width:768px)
{
.footer-container
{
    max-width: 90%;
    grid-template-columns:1fr;

}
}
