.mask{
    width: 100%;
    height: 100%;
    position: relative;
}
.About-img{
width: 100%;
height: 100%;
background-size: cover;
background-position: center;
object-fit: cover;

}
mask::before{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    left:0;
    background-color: black;
    opacity: 0.8;
}
.hero{
    height: 100%;
    width: 100%;
}
.hero .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.hero .content{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}
.hero .content p{
    font-size: 1.4rem;
    font-weight: 400;
    text-transform: uppercase;
}
.hero .content h1{
    font-size: 2rem;
    font-weight: 400;
    text-transform: uppercase;
}
@media screen and (max-width: 640px) {
    .About-img{
     width:100%;
     /* height:100vh; */
     height: 100%;
        }
        .hero .content{
            display: none;
        }
