.about{
    width: 100%;
    margin:3rem 0;
    display: flex;
    align-items:center;
    justify-content:center;
    flex-wrap: wrap;
    height: 100%;
}
.about .left{
    text-align: center;
    margin:auto;
    padding: 1rem;
    max-width: 350px;
}
.about .left p{
    margin:1.2rem 0;
}
.about .right{
    max-width: auto;
}
.about .right .img{
    width: 70%;
    left: 10%;
}

